
import { defineComponent, ref } from "vue";

export default defineComponent({
  name: "HomeIcon",

  props: {
    title: {
      type: String,
      default: "",
    },
    colorType: {
      type: String,
      default: "Light", // Light | Dark
    },
    fontSize: {
      type: String,
      default: "20px",
    },
    fontWeight: {
      type: String,
      default: "300",
    },
  },

  setup(props) {
    const showTitle = ref(props.title === undefined ? false : true);

    return {
      showTitle,
    };
  },
});
