export function getSupportModalities(NeedWildCard: boolean): string[] {
  if (NeedWildCard) {
    return ["*", "DX", "CR", "DR", "CT", "MR", "MG", "IO"];
  }

  return ["DX", "CR", "DR", "CT", "MR", "MG", "IO"];
}
