
import { defineComponent, PropType } from "vue";
import * as myTypes from "@/types";

export default defineComponent({
  name: "UserSettingMenuItem",

  props: {
    item: {
      type: Object as PropType<myTypes.UserSettingMenuItem>,
      required: true,
    },
    level: {
      type: Number,
      default: 1,
    },
  },

  emits: ["active-item"],

  setup() {
    return {};
  },
});
